.hljs {
  display: block;
  overflow-x: auto;
  padding: 16px 16px;
  background: #1e1e1e;
  color: #dcdcdc;
  font-family: $code-font-family;
  font-size: 0.85rem;
  border-radius: 4px;
}

.hljs-keyword, .hljs-literal, .hljs-name, .hljs-symbol {
  color: #569cd6;
  font-family: $code-font-family;
}

.hljs-link {
  color: #569cd6;
  text-decoration: underline
}

.hljs-built_in, .hljs-type {
  color: #4ec9b0
}

.hljs-class, .hljs-number {
  color: #b8d7a3
}

.hljs-meta-string, .hljs-string {
  color: #d69d85
}

.hljs-regexp, .hljs-template-tag {
  color: #9a5334
}

.hljs-formula, .hljs-function, .hljs-params, .hljs-subst, .hljs-title {
  color: #dcdcdc;
  font-family: $code-font-family;
}

.hljs-comment, .hljs-quote {
  color: #898989;
  font-family: $code-font-family;
}

.hljs-doctag {
  color: #608b4e
}

.hljs-meta, .hljs-meta-keyword, .hljs-tag {
  color: #9b9b9b
}

.hljs-template-variable, .hljs-variable {
  color: #bd63c5
}

.hljs-attr, .hljs-attribute, .hljs-builtin-name {
  color: #9cdcfe
}

.hljs-section {
  color: gold
}

.hljs-emphasis {
  font-style: italic
}

.hljs-strong {
  font-weight: 700
}

.hljs-bullet, .hljs-selector-attr, .hljs-selector-class, .hljs-selector-id, .hljs-selector-pseudo, .hljs-selector-tag {
  color: #d7ba7d;
  font-family: $code-font-family;
}

.hljs-addition {
  background-color: #144212;
  display: inline-block;
  width: 100%
}

.hljs-deletion {
  background-color: #600;
  display: inline-block;
  width: 100%
}

.hljs-ln-numbers {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  text-align: center;
  color: #aaaaaa;
  border-left: 4px solid #1e1e1e;
  border-right: 20px solid #1e1e1e;
  vertical-align: center;
  font-size: 0.7rem;
  font-family: $code-font-family;
  @media (max-width: $break-small) {
    font-size: 0.65rem;
  }
}

.hljs-ln-code {
  border-right: 20px solid #1e1e1e;
  font-family: $code-font-family !important;
}

.csharp {
  font-family: $code-font-family !important;
}

.kn, .nn, .o, .k, .nc, .n, .na, .s, .hljs-string, .hljs-number, .kd, .hljs-function, .hljs-params, .hljs-built_in, .hljs-ln-line, .hljs-attribute, .hljs-meta {
  font-family: $code-font-family !important;
}