footer {
  width: 100%;
  height: 128px;
  margin-top: 56px;

  p {
    font-size: 0.75rem;
    color: $primary-color;
    text-align: center;
    margin-top: 24px;
  }

  .social {
    display: flex;
    top: 0;
    margin-top: 36px;
    z-index: 100;
    justify-content: center;
  }

  .social-icons {
    display: flex;
    width: 700px;
    max-width: 90%;
    height: 100%;
    align-items: center;
    justify-content: center;

    ul {
      list-style: none;
      margin: 0;
      padding: 0;
    }

    li {
      display: inline-block;
      padding-left: 16px;
      padding-right: 16px;
      -moz-user-select: none;
      -khtml-user-select: none;
      -webkit-user-select: none;
      -ms-user-select: none;
      user-select: none;
      transition: 0.3s;
    }

    li:hover {
      opacity: 0.5;
    }

    img {
      pointer-events: none;
      margin: 0;
      padding: 0;
    }
  }

  .to-top {
    position: relative;
    width: 42px;
    height: 42px;
    float: right;
    padding: 16px;
    margin-right: 16px;
    border: none;
    outline: none;
    cursor: pointer;
    background-color: $primary-color;
    border-radius: 50%;
    opacity: 1 !important;
    transition: .2s !important;

    @media (max-width: $break-small) {
      width: 36px;
      height: 36px;
    }
  }

  .to-top::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 42px;
    height: 42px;
    background-color: white;
    mask-image: url(/assets/svg/chevron-up-outline.svg);
    mask-size: 28px 28px;
    mask-position: center;
    mask-repeat: no-repeat;
    -webkit-mask-size: 28px 28px;
    -webkit-mask-image: url(/assets/svg/chevron-up-outline.svg);
    -webkit-mask-position: center;
    -webkit-mask-repeat: no-repeat;

    @media (max-width: $break-small) {
      width: 36px;
      height: 36px;
      mask-size: 20px 20px;
      -webkit-mask-size: 20px 20px;
    }
  }

  .to-top:hover {
    opacity: .8 !important;
  }
}